*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
}

input {
  outline: none;
}

a {
  color: inherit;
  text-decoration: none;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.catalog {
  background-color: #111;
}

.invisible-title {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.gallery {
  flex-wrap: wrap;
  list-style: none;
  display: flex;
}

.gallery-item {
  width: 395px;
  height: 574px;
  background-repeat: no-repeat;
  background-size: cover;
  flex-basis: calc(33.3333% - 10.3333px);
  margin-bottom: 32px;
  margin-right: 15px;
  position: relative;
}

.gallery > .gallery-item:nth-child(3n) {
  margin-right: 0;
}

.gallery > .gallery-item:nth-last-child(-n+1) {
  margin-bottom: 60px;
}

@media screen and (min-width: 768px) {
  .gallery > .gallery-item:nth-last-child(-n+1) {
    margin-bottom: 44px;
  }
}

@media screen and (min-width: 320px) and (max-width: 480px) {
  .gallery > .gallery-item:nth-last-child(-n+1) {
    margin-bottom: 20px;
  }
}

.gallery-item__about {
  width: 260px;
  position: absolute;
  bottom: 20px;
  left: 20px;
}

@media screen and (min-width: 320px) and (max-width: 480px) {
  .gallery-item__about {
    bottom: 12px;
    left: 12px;
  }
}

.gallery-item__about__title, .gallery-item__about__p {
  color: #fff;
  text-transform: uppercase;
  font-size: 20px;
  font-weight: 500;
  line-height: 1.17;
}

@media screen and (min-width: 768px) {
  .gallery-item__about__title, .gallery-item__about__p {
    font-size: 12px;
    line-height: 1.5;
  }
}

@media screen and (min-width: 320px) and (max-width: 480px) {
  .gallery-item__about__title, .gallery-item__about__p {
    line-height: 1.67;
  }
}

.gallery-item__about__p {
  color: #b7b7b7;
  text-transform: none;
}

.vote-cinemas {
  width: 106px;
  height: 18px;
  pointer-events: none;
  position: absolute;
  bottom: 20px;
  right: 20px;
}

