.hero-trend__wrap {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  height: 100%;
  max-height: 750px;
  overflow: hidden;
  background-color: #121212;

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    opacity: 0.5;
    z-index: -1;
  }

  .hero-trend__details {
    margin-top: auto;
    text-align: center;
    color: #fff;

    .hero-trend__title {
      font-size: 2.2rem;
      font-weight: bold;
      margin-bottom: 0.5rem;
    }

    .hero-trend__rating {
      font-size: 1.2rem;
      margin-bottom: 0.5rem;
    }

    .hero-trend__overview {
      font-size: 1rem;
      margin-bottom: 1.5rem;
    }

    .hero-trend__btn {
      padding: 0.5rem 1rem;
      font-size: 1.1rem;
      font-weight: bold;
      color: #fff;
      background-color: #f54642;
      border: none;
      border-radius: 4px;
      cursor: pointer;

      &:hover {
        background-color: #e53935;
      }
    }
  }

  @media screen and (min-width: 576px) {
    .hero-trend__details {
      .hero-trend__title {
        font-size: 2.8rem;
      }

      .hero-trend__rating {
        font-size: 1.5rem;
      }

      .hero-trend__overview {
        font-size: 1.2rem;
      }

      .hero-trend__btn {
        font-size: 1.3rem;
      }
    }
  }

  @media screen and (min-width: 992px) {
    .hero-trend__details {
      .hero-trend__title {
        font-size: 3.5rem;
      }

      .hero-trend__rating {
        font-size: 1.8rem;
      }

      .hero-trend__overview {
        font-size: 1.4rem;
      }

      .hero-trend__btn {
        font-size: 1.5rem;
      }
    }
  }
}

