  /* or any other value that suits your design */
  overflow: hidden;
}

.hero-trend__details {
  position: absolute;
  z-index: 1;
  color: #fff;
  text-align: center;
  width: 100%;
  padding: 0 20px;
  max-width: 800px;
  text-shadow: 0px 0px 20px rgba(0, 0, 0, 0.826);
}

.hero-trend__title {
  font-size: 4rem;
  margin: 0 0 10px;
}

.hero-trend__rating {
  font-size: 1.5rem;
  margin: 0 0 10px;
}

.hero-trend__overview {
  font-size: 1.2rem;
  margin: 0 0 20px;
  line-height: 1.5;
}

.hero-trend__btn {
  display: inline-block;
  padding: 10px 20px;
  background-color: #f4900d;
  color: #000;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1.2rem;
  transition: all 0.3s ease;
}

.hero-trend__btn:hover {
  background-color: #000;
  color: #fff;
}

.hero-trend__image {
  position: absolute;
  z-index: 0;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.hero-trend__image img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}
